











import Vue from "vue";
import day from "dayjs";
import { CommonMutations } from "./common.mutations";
import { CommonStore } from "./common.store";

export default Vue.extend({
  computed: {
    curYear() {
      return day().year();
    }
  },
  methods: {
    onSearchClick() {
      CommonStore.commit(CommonMutations.SET_SEARCH_BAR_VISIBLE, true);
    }
  }
});
